import React from "react";
import { Box, Typography, Grid, Paper, Container } from "@mui/material";
import { motion } from "framer-motion";

const AboutUs = () => {
  return (
    <Box
      sx={{
        py: 8,
        px: 4,
        backgroundColor: "#f4f7fb",
        textAlign: "center",
        color: "#333",
      }}
    >
      <Container>
        {/* <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1 }}
        >
          <Typography
            variant="h3"
            fontWeight="bold"
            sx={{
              mb: 6,
              color: "#1e3c72",
              textShadow: "2px 2px 4px rgba(0, 0, 0, 0.2)",
              fontFamily: "'Poppins', sans-serif",
            }}
          >
            About SMINTECH24
          </Typography>
        </motion.div> */}

        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1, delay: 0.3 }}
        >
          <Typography
            variant="h6"
            sx={{
              mb: 6,
              color: "#555",
              fontFamily: "'Roboto', sans-serif",
              fontStyle: "italic",
            }}
          >
            SMINTECH24 was founded in 2024 in Tiruvannamalai, Tamil Nadu with a
            vision to provide innovative and affordable webtech solutions to
            small businesses.
          </Typography>
        </motion.div>

        {/* Motion effect added to Core Values text */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1, delay: 0.5 }}
        >
          <Typography
            variant="h5"
            sx={{
              mb: 4,
              fontWeight: "bold",
              color: "#1e3c72",
            }}
          >
            Our Core Values
          </Typography>
        </motion.div>

        <Grid container spacing={4} justifyContent="center">
          {["Innovation", "Trust", "Quality", "Customer Focus"].map(
            (value, index) => (
              <Grid item xs={12} sm={6} md={3} key={index}>
                <motion.div
                  initial={{ opacity: 0, y: 50 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 1, delay: 0.5 + index * 0.3 }}
                >
                  <Paper
                    sx={{
                      p: 4,
                      borderRadius: 3,
                      backgroundColor: "#ffffff",
                      boxShadow: "0 6px 12px rgba(0,0,0,0.1)",
                      transition: "transform 0.3s ease, box-shadow 0.3s ease",
                      "&:hover": {
                        transform: "translateY(-8px)",
                        boxShadow: "0 12px 24px rgba(0,0,0,0.15)",
                      },
                    }}
                  >
                    <Typography
                      variant="h6"
                      fontWeight="600"
                      sx={{
                        mb: 2,
                        color: "#1e3c72",
                      }}
                    >
                      {value}
                    </Typography>
                    <Typography
                      variant="body2"
                      sx={{
                        color: "#777",
                      }}
                    >
                      Our commitment to {value.toLowerCase()} ensures that we
                      deliver exceptional results to our clients with every
                      project.
                    </Typography>
                  </Paper>
                </motion.div>
              </Grid>
            )
          )}
        </Grid>

        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1, delay: 1 }}
        >
          <Typography
            variant="h5"
            sx={{
              mt: 8,
              fontWeight: "bold",
              color: "#1e3c72",
            }}
          >
            Join Us in Making a Difference
          </Typography>
          <Typography
            variant="body1"
            sx={{
              mt: 3,
              color: "#555",
              fontFamily: "'Roboto', sans-serif",
              fontStyle: "italic",
            }}
          >
            SMINTECH24 is on a mission to help small businesses grow by
            leveraging the power of technology. Our innovative solutions are
            designed with your success in mind. Join us in transforming your
            business with tailored webtech solutions that drive results.
          </Typography>
        </motion.div>
      </Container>
    </Box>
  );
};

export default AboutUs;
