import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import HeroSection from "./herosection";
import IndustriesWeServe from "./industriesWeServe";
import OurProcess from "./ourProcess";
import WhyChooseSection from "./whyChooseSection";

const Home = () => {
  const [activeSection, setActiveSection] = useState("why");

  const renderSection = () => {
    switch (activeSection) {
      case "why":
        return <WhyChooseSection />;
      case "process":
        return <OurProcess />;
      case "industries":
        return <IndustriesWeServe />;
      default:
        return null;
    }
  };

  const fadeInUp = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: { duration: 0.5 },
  };

  const tabs = [
    { key: "why", label: "Why smintech" },
    { key: "process", label: "Our process" },
    { key: "industries", label: "Industries we serve" },
  ];

  // Set background color dynamically based on the active section
  const getSectionBackgroundColor = (section) => {
    switch (section) {
      case "why":
        return "#f4f7fb"; // Background for 'Why Choose'
      case "process":
        return "#f2fafa"; // Background for 'Our Process'
      case "industries":
        return "#f0fdf9"; // Background for 'Industries We Serve'
      default:
        return "#fff";
    }
  };

  return (
    <>
      <HeroSection />

      <div
        style={{
          ...styles.tabContainer,
          backgroundColor: getSectionBackgroundColor(activeSection),
          transition: "background-color 0.3s ease",
        }}
      >
        {tabs.map((tab) => (
          <span
            key={tab.key}
            onClick={() => setActiveSection(tab.key)}
            style={{
              ...styles.tab,
              color: activeSection === tab.key ? "#1976d2" : "#333",
            }}
          >
            <motion.div whileHover={{ scale: 1.05 }} style={styles.tabInner}>
              {tab.label}
              {activeSection !== tab.key && (
                <ArrowForwardIosIcon
                  style={{
                    fontSize: "16px",
                    marginLeft: "6px",
                    verticalAlign: "middle",
                  }}
                />
              )}
              <div
                style={{
                  ...styles.underline,
                  width: activeSection === tab.key ? "100%" : "0%",
                }}
              />
            </motion.div>
          </span>
        ))}
      </div>

      <div
        style={{
          padding: "0 1rem",
          marginTop: "0", // Remove any top margin here to eliminate space
          backgroundColor: getSectionBackgroundColor(activeSection),
          transition: "background-color 0.3s ease",
        }}
      >
        <AnimatePresence mode="wait">
          <motion.div
            key={activeSection}
            {...fadeInUp}
            style={{
              minHeight: "auto", // Ensure this doesn't add any unnecessary space
              marginTop: "0", // Remove top margin here too
            }}
          >
            {renderSection()}
          </motion.div>
        </AnimatePresence>
      </div>
    </>
  );
};

const styles = {
  tabContainer: {
    display: "flex",
    justifyContent: "center",
    gap: "2rem",
    marginTop: "0", // Remove top margin to avoid extra space
    paddingBottom: "0.5rem",
    flexWrap: "wrap",
    padding: "1rem",
    marginBottom: "0", // Remove any bottom margin
  },
  tab: {
    cursor: "pointer",
    fontSize: "1.1rem",
    fontWeight: "500",
    position: "relative",
    display: "inline-block",
    transition: "color 0.3s ease",
    padding: "8px 16px",
    borderRadius: "5px",
  },
  tabInner: {
    display: "inline-flex",
    alignItems: "center",
    position: "relative",
    paddingBottom: "4px",
  },
  underline: {
    height: "2px",
    backgroundColor: "#1976d2",
    position: "absolute",
    bottom: 0,
    left: 0,
    transition: "width 0.3s ease",
  },
};

export default Home;
