import React from "react";
import {
  Box,
  Container,
  Grid,
  Typography,
  Stack,
  Link as MuiLink,
} from "@mui/material";
import EmailIcon from "@mui/icons-material/Email";
import PhoneIcon from "@mui/icons-material/Phone";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import InstagramIcon from "@mui/icons-material/Instagram";
import LinkedInIcon from "@mui/icons-material/LinkedIn";

const Contact = () => {
  return (
    <Box
      sx={{
        background: "#f9f9f9",
        color: "#333",
        py: { xs: 4, sm: 6 },
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4} alignItems="stretch">
          {/* Left: Info Card */}
          <Grid item xs={12} md={6} sx={{ display: "flex" }}>
            <Box
              sx={{
                backgroundColor: "#fff",
                p: 4,
                borderRadius: 2,
                boxShadow: "0 4px 12px rgba(0,0,0,0.08)",
                flexGrow: 1,
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                minHeight: { xs: 300, md: 400 }, // Fixed height to match
              }}
            >
              <Stack spacing={2.5}>
                <Stack direction="row" spacing={2} alignItems="flex-start">
                  <LocationOnIcon sx={{ color: "#1976d2", mt: 0.5 }} />
                  <Typography variant="body1">
                    <strong>
                      SMINTECH24 SOFTWARE TECHNOLOGIES PRIVATE LIMITED
                    </strong>
                    <br />
                    203, Bharathidasan Nagar, Arunagiripuram,
                    <br />
                    Thiruvannamalai, Tamil Nadu 606601.
                  </Typography>
                </Stack>

                <Stack direction="row" spacing={2} alignItems="center">
                  <EmailIcon sx={{ color: "#1976d2" }} />
                  <MuiLink
                    href="mailto:info@smintech24.com"
                    underline="hover"
                    color="inherit"
                  >
                    info@smintech24.com
                  </MuiLink>
                </Stack>

                <Stack direction="row" spacing={2} alignItems="center">
                  <PhoneIcon sx={{ color: "#1976d2" }} />
                  <MuiLink
                    href="tel:6385473308"
                    underline="hover"
                    color="inherit"
                  >
                    +91 63854 73308
                  </MuiLink>
                </Stack>

                <Stack direction="row" spacing={2} alignItems="center">
                  <InstagramIcon sx={{ color: "#E1306C" }} />
                  <MuiLink
                    href="https://instagram.com/smintech24_official"
                    target="_blank"
                    underline="hover"
                    color="inherit"
                  >
                    @smintech24_official
                  </MuiLink>
                </Stack>

                <Stack direction="row" spacing={2} alignItems="center">
                  <LinkedInIcon sx={{ color: "#0077B5" }} />
                  <MuiLink
                    href="https://www.linkedin.com/company/smintech24-software"
                    target="_blank"
                    underline="hover"
                    color="inherit"
                  >
                    smintech24 software
                  </MuiLink>
                </Stack>
              </Stack>
            </Box>
          </Grid>

          {/* Right: Google Map */}
          <Grid item xs={12} md={6} sx={{ display: "flex" }}>
            <Box
              sx={{
                flexGrow: 1,
                borderRadius: 2,
                overflow: "hidden",
                boxShadow: "0 4px 12px rgba(0,0,0,0.08)",
                minHeight: { xs: 300, md: 400 }, // Match the left side height
              }}
            >
              <iframe
                title="Smintech Location"
                width="100%"
                height="100%"
                style={{ border: 0 }}
                loading="lazy"
                allowFullScreen
                referrerPolicy="no-referrer-when-downgrade"
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3899.433142256146!2d79.06843188218389!3d12.218924457171696!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bacbfc9b5bc08b3%3A0x7f596ecfbb0d8da3!2sSMINESERVICES!5e0!3m2!1sen!2sin!4v1746444712706!5m2!1sen!2sin"
              ></iframe>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Contact;
