import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import {
  Box,
  Container,
  TextField,
  Grid,
  Typography,
  Button,
  Paper,
} from "@mui/material";

const Career = () => {
  const [activeTab, setActiveTab] = useState("internship");

  const fadeInUp = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
    transition: { duration: 0.5 },
  };

  const tabs = [
    { key: "internship", label: "Internship" },
    { key: "openings", label: "Openings" },
  ];

  const renderSection = () => {
    switch (activeTab) {
      case "internship":
        return (
          <Paper elevation={2} sx={{ p: 4, mt: 2 }}>
            <Typography variant="h6" gutterBottom sx={{ color: "#004b87" }}>
              Submit the form and we'll get back to you soon!
            </Typography>
            <Typography variant="body2" mb={3} color="text.secondary">
              We're always looking for enthusiastic interns ready to learn and
              grow with us. Fill out the form below to apply.
            </Typography>
            <Box component="form" onSubmit={(e) => e.preventDefault()}>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <TextField fullWidth label="Full Name" required />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Email Address"
                    type="email"
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Phone Number"
                    type="tel"
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField fullWidth label="Education / Degree" />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Why do you want to join our internship?"
                    multiline
                    rows={4}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Typography mb={1}>Upload Resume:</Typography>
                  <input type="file" accept=".pdf,.doc,.docx" required />
                </Grid>
                <Grid item xs={12}>
                  <Box textAlign="center">
                    <Button variant="contained" size="large" type="submit">
                      Submit Application
                    </Button>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          </Paper>
        );
      case "openings":
        return (
          <Box mt={2}>
            <Paper elevation={2} sx={{ p: 4 }}>
              <Typography variant="h6" gutterBottom sx={{ color: "#004b87" }}>
                Job Openings
              </Typography>
              <Typography variant="body1" color="text.secondary">
                Currently, there are no job openings. Please check back later.
              </Typography>
            </Paper>
          </Box>
        );
      default:
        return null;
    }
  };

  return (
    <Box sx={{ py: 6, backgroundColor: "#f9fafb", minHeight: "100vh" }}>
      <Container maxWidth="md">
        <Typography
          variant="h4"
          fontWeight="bold"
          textAlign="center"
          gutterBottom
          sx={{ color: "#004b87" }}
        >
          Career Opportunities at SMINTECH24
        </Typography>
        <Typography textAlign="center" mb={4} color="#555">
          Join our growing team of passionate developers, designers, and
          innovators!
        </Typography>

        <div style={styles.tabContainer}>
          {tabs.map((tab) => (
            <span
              key={tab.key}
              onClick={() => setActiveTab(tab.key)}
              style={{
                ...styles.tab,
                color: activeTab === tab.key ? "#1976d2" : "#333",
              }}
            >
              <motion.div whileHover={{ scale: 1.05 }} style={styles.tabInner}>
                {tab.label}
                {activeTab !== tab.key && (
                  <ArrowForwardIosIcon
                    style={{
                      fontSize: "16px",
                      marginLeft: "6px",
                      verticalAlign: "middle",
                    }}
                  />
                )}
                <div
                  style={{
                    ...styles.underline,
                    width: activeTab === tab.key ? "100%" : "0%",
                  }}
                />
              </motion.div>
            </span>
          ))}
        </div>

        <AnimatePresence mode="wait">
          <motion.div
            key={activeTab}
            {...fadeInUp}
            style={{ marginTop: "1rem" }}
          >
            {renderSection()}
          </motion.div>
        </AnimatePresence>
      </Container>
    </Box>
  );
};

const styles = {
  tabContainer: {
    display: "flex",
    justifyContent: "center",
    gap: "2rem",
    flexWrap: "wrap",
    padding: "1rem",
  },
  tab: {
    cursor: "pointer",
    fontSize: "1.1rem",
    fontWeight: "500",
    position: "relative",
    display: "inline-block",
    transition: "color 0.3s ease",
    padding: "8px 16px",
    borderRadius: "5px",
  },
  tabInner: {
    display: "inline-flex",
    alignItems: "center",
    position: "relative",
    paddingBottom: "4px",
  },
  underline: {
    height: "2px",
    backgroundColor: "#1976d2",
    position: "absolute",
    bottom: 0,
    left: 0,
    transition: "width 0.3s ease",
  },
};

export default Career;
